#site-header-inner .mr-auto.navbar-nav a{font-family: "Montserrat", sans-serif;letter-spacing: 1px;font-size: 13px;color: #ffffff;text-transform: uppercase;font-weight: bold;margin: 0 29.7px;line-height: 100px;position: relative;}  
 #site-header-inner .navbar{padding: 0;}
 #site-header-inner .navbar-brand{margin-right: 0;}
 #site-header.fixed{position: fixed !important;left: 0;top: 0; width: 100%; z-index: 9989;}
 .align-vertical.container .btn.btn-primary.btn-white{background: transparent;color: #fff;border: 2px solid #fff;}
 .container .btn.btn-primary.btn-white:hover{background: #fff;color: #000;border: none;}
 .btn-primary.btn-filled:hover{border-color:transparent}
 .hero-slider .slides li.overlay::before { position: absolute;width: 100%;height: 100%;top: 0;content: '';opacity: .3;z-index: 2;background-color: #222;}
 .owl-carousel .owl-nav button.owl-prev,.owl-carousel .owl-nav button.owl-next{background-color: #00abc9!important;border-radius: 50%!important;width: 32px;
    height: 32px;color: #fff!important;font-size: 29px !important;}
 .owl-theme .owl-nav{position: absolute;right: 0;top: -70px;}
 .owl-theme .owl-nav .owl-prev{right: 10px;}
 .owl-carousel .owl-nav button span{position: absolute; top: 0px; right: 10px;}

 #inner_pages #site-header-inner .mr-auto.navbar-nav a{color: #777;}
#site-header-inner .mr-auto.navbar-nav a:hover::before{content: "";
    width: 100%;
    height: 3px;
    background-color: #00abc9;
    position: absolute;
    bottom: 0;
    left: 0;
    transition: all 0.3s ease 0s;
    -webkit-transition: all 0.3s ease 0s;
    -moz-transition: all 0.3s ease 0s;}
 #inner_pages #site-header{background-color: #fff;z-index: 99;box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.15);color: #2b323a;}

 /* innerpages */
 #inner_pages #top-bar-inner{display: none}
 .Breadcrumb_main{padding-top: 125px;}
 .breadcrumb{background-color: transparent;float: right;padding:0 1rem;}
 .breadcrumb-item + .breadcrumb-item::before{content: "\f054";font-family: 'financeicons' !important;padding: 0 2px 0 6px;
    color: #00abc9;font-size: 10px;}
.themesflat-button.blue {background-color: #00abc9;color: #fff;}
.themesflat-button.blue:hover {
    background-color: #333;
    color: #fff;
}
.feature-about-us img{width: 100%;}
.feature-about-us{padding: 25px;}
.nav-tabs .nav-link{padding: 10px 36px 8px;padding: 10px 40px 8px;
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    line-height: 30px;
    color: #999;
    list-style: none;
    margin: 0 2px 0 0;
    display: inline-block;
    background-color: #ebebeb}   
.tab-content > .tab-pane{padding: 33px 0 0;}
.tab-content > .tab-pane p,.tab-content > .tab-pane ul{font-size: 17px;}
.themesflat-tabs .tab-content .item-content .heading {
    font-size: 18px;
    line-height: 30px;
    color: #2b323a;
    margin-bottom: 25px;}
.themesflat-tabs .tab-content .item-content ul li i { padding-right: 15px;color: #00abc9;}
.item-content ul {  list-style: none; margin: 0;  }
.half_background_style2{
background-image: url(/Requeforts/static/media/bg_video2.911c395a.jpg);
background-size: cover;
overflow: hidden;
background-position: center;
background-repeat: no-repeat;}

.themesflat-team .socials {
    width: 100%;
    text-align: center;
    z-index: 2;
    position: absolute;
    left: 0;
    top: 50%;
    margin-top: -17px;
    list-style: none;margin: 0;
}
.themesflat-team .text-wrap {
    text-align: center;
    padding: 25px 0 0;
}
.aboutus_page_slider .text-wrap{text-align: center;padding: 25px 0 0;}
.themesflat-team .thumb {
    position: relative;
    overflow: hidden;
}
.themesflat-team .team-item:hover .socials li:nth-child(1) {
    transition-delay: 0.15s;
}
.nw_services_row  .thumb {
    position: relative;
    overflow: hidden;
}
.nw_services_row .text-wrap {
    padding: 22px 31px 29px 26px;
    letter-spacing: 0.1px;
}
.best_service_section h3.title {
    font-size: 15px;
    margin-bottom: 0px;
    padding: 8px 25px;
        padding-top: 8px;
        padding-bottom: 8px;
    letter-spacing: 0.7px;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    color: #fff;
    background: -webkit-linear-gradient(-90deg, transparent, rgba(0,0,0,0.8) ) !important;text-transform: uppercase;
}
.best_service_section .title a:hover {
    color: #00abc9;
}
.best_service_section .simple-link::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 3px;
    width: 100%;
    height: 1px;
    background-color: #00abc9;
    transition: all ease 0.3s;
}
.best_service_section .simple-link:hover::after {
    width: 0;
}
.best_service_section .simple-link {
    font-size: 12px;
    font-weight: 500;
    display: inline-block;
    margin-top: 15px;
    color: #00abc9;
    position: relative;
    font-family: "Montserrat";
}
.best_service_section  .owl-theme .owl-dots {
    padding-top: 34px;
}
.bg-parallax-overlay.overlay-black {
    background-color: rgba(29,38,48,0.95);
}
.themesflat-headings.color-white .heading, .themesflat-headings.color-white .sub-heading {
    color: #fff;
}
.form-group select{padding: 9px 19px;height: 50px;background-color: transparent;font-size: 14px}
.themesflat-contact-form .themesflat-button.blue{padding: 17px 53px;}
.team-member.animate.fadeInUp ul.social-text{list-style: none;}
.team-member .social-text {display: flex;}
.team-member ul.social-text li {margin: 0 10px 0 10px;}
.latest_news_content .latest_news_meta span img{width: auto;}
.themesflat-contact-info .contact-info-item .title > span::after { width: 53px;height: 2px;
    background-color: #00abc9;
    content: "";
    position: absolute;
    left: 0;
    top: 100%;
    z-index: 1;
}
.themesflat-contact-info .contact-info-item .title {
    font-size: 14px;
    color: #222;
    font-weight: 600;
    z-index: 1;
    letter-spacing: 0.5px;
    margin-bottom: 19px;
    position: relative;
}
.themesflat-contact-info .contact-info-item {
    margin-top: 13px;
}
.themesflat-contact-info .contact-info-item .socials a {
    font-size: 17px;
    background-color: #ebebeb;
    color: #898989;
    display: inline-block;
    width: 37px;
    height: 37px;
    line-height: 37px;
    text-align: center;margin:2px;
}
.themesflat-contact-info ul{padding:0;margin: 0;}
.themesflat-contact-info li{list-style-type: none;}
.themesflat-contact-info li a{color: #5a5a5a;}
textarea.form-control{font-size: 16px;}
.themesflat-button.blue{padding: 17px 56px;}
#latest_news a{text-decoration: none;}
.counter{background-color: rgba(29,38,48,0.95);}
.counter_div{border:1px solid #3c444d;padding: 40px 40px}
.counter_div .icon {
    font-size: 80px;
    line-height: normal;
    margin: -10px 6px 0 16px;
    color: #454b51;
}
.counter_div .suffix {
    color: #00abc9;
    margin-left: 5px;
}
.counter_div .number-wrap{
font-size: 35px;
letter-spacing: 1px;
}
@media only screen and (max-width: 767px) {
    .hero-slider .slides li h1{font-size: 20px; line-height: normal;margin-bottom: 30px;}
    .theme-counter h2{font-size: 20px;}
    .hero-slider .slides li{height: auto;}
    #site-header-inner .mr-auto.navbar-nav a{line-height: 50px!important;}
    #site-header-inner{margin: 0 15px;}
    .Breadcrumb_main{padding-top: 97px;}
    .breadcrumb{float: left;
        padding: 0 0;
        margin: 0 15px;}
        #featured-title .featured-title-heading{margin: 0 15px; padding: 0;}
        #site-header-inner .mr-auto.navbar-nav a{padding-left:0;}
        .navbar-collapse{background: #222;} 
        #site-logo{margin-bottom: 10px;}   
        #site-header-inner .mr-auto.navbar-nav a:hover::before{width: 20%;}
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

